<template>
    <div class="answertitle">
        <div class="title">
            <i @click="goState" class="fa fa-angle-left"></i>
            <h3 v-if="hsMessageactive">{{hsMessage}}</h3>
            <h3 v-else>
                {{mins}}:{{secounds}}
                <span v-if="testtimevalue">{{testTime}}</span>
            </h3>
            <span>{{spanMessage}}</span>
        </div>
    </div>
</template>
<script>
    export default {
        props: ['spanMessage', 'hsMessage', 'testTime', 'testtimevalue','hsMessagevalue','hsMessageactive'],
        name: "answertitle",
        data() {
            return {
                mins: null,
                secounds: null
            }
        },
        mounted() {
            this.mins = parseInt(this.hsMessage / 60),
            this.secounds = parseInt(this.hsMessage % 60)
            if (this.secounds < 10) {
                this.secounds = '0' + this.secounds
            }
            if (this.mins < 10) {
                this.mins = '0' + this.mins
            }
        },
    
        watch: {
            'hsMessage'() {
                this.mins = parseInt(this.hsMessage / 60),
                    this.secounds = parseInt(this.hsMessage % 60)
                if (this.secounds < 10) {
                    this.secounds = '0' + this.secounds
                }
                if (this.mins < 10) {
                    this.mins = '0' + this.mins
                }
            }
        },
        methods: {
            goState() {
                this.$router.push(this.hsMessagevalue)
            }
        }
    }
</script>

<style scope lang="less">
    .answertitle {
        width: 100%;

        .title {
            padding: 1rem;
            font-size: 1.5rem;
            display: flex;
            line-height: 2.48rem;
            border-bottom: 0.1rem solid lightgray;
            i {
                font-size: 2.5rem;
                width: 100%;
            }

            h3 {
                width: 100%;
                text-align: center;
            }

            span {
                width: 100%;
                text-align: right;
            }
        }
    }
</style>